'use client';

import React, { useState, useMemo } from 'react';
import { Plus, Edit, Trash2, Search, ChevronLeft, ChevronRight, FileX } from 'lucide-react';
import SidebarOnly from '@/components/sidebar'

interface Course {
  id: number;
  class_date: string;
  session: string;
  start_time: string;
  end_time: string;
  menu_id: number;
  capacity: number;
  price: number;
  is_special: boolean;
  enrolled?: number;
  detail?: string;
}

interface FormData extends Omit<Course, 'id'> {}

export default function ManageCourses() {
  const [courses, setCourses] = useState<Course[]>([
    {
      id: 1,
      class_date: '2025-11-12',
      session: 'Thai Herbs & Spices Cooking Class',
      start_time: '16:00',
      end_time: '18:00',
      menu_id: 101,
      capacity: 12,
      price: 1500,
      is_special: false,
      enrolled: 8,
      detail: '<h3>Course Overview</h3><p>Learn the art of Thai cooking with fresh herbs and aromatic spices.</p>'
    },
    {
      id: 2,
      class_date: '2025-11-13',
      session: 'Massaman Thai Cooking',
      start_time: '16:00',
      end_time: '18:00',
      menu_id: 102,
      capacity: 10,
      price: 1200,
      is_special: false,
      enrolled: 10,
      detail: '<p>Master the rich and creamy <strong>Massaman Curry</strong> recipe.</p>'
    },
    {
      id: 3,
      class_date: '2025-11-12',
      session: 'Coconut & Curry Cooking Experience',
      start_time: '16:00',
      end_time: '18:00',
      menu_id: 103,
      capacity: 15,
      price: 1800,
      is_special: true,
      enrolled: 12
    },
    {
      id: 4,
      class_date: '2025-11-12',
      session: 'Thai Curry Masterclass',
      start_time: '16:00',
      end_time: '18:00',
      menu_id: 104,
      capacity: 10,
      price: 1600,
      is_special: false,
      enrolled: 7
    },
    {
      id: 5,
      class_date: '2025-11-13',
      session: 'Thai Street Food Favorites',
      start_time: '16:00',
      end_time: '18:00',
      menu_id: 105,
      capacity: 12,
      price: 1400,
      is_special: false,
      enrolled: 9
    },
    {
      id: 6,
      class_date: '2025-11-13',
      session: 'Sweet Thai Classics Workshop',
      start_time: '15:00',
      end_time: '17:00',
      menu_id: 106,
      capacity: 8,
      price: 1300,
      is_special: true,
      enrolled: 6
    },
        {
      id: 7,
      class_date: '2025-11-13',
      session: 'Sweet Thai Classics Workshop',
      start_time: '15:00',
      end_time: '17:00',
      menu_id: 106,
      capacity: 8,
      price: 1300,
      is_special: true,
      enrolled: 6
    },
        {
      id: 8,
      class_date: '2025-11-13',
      session: 'Sweet Thai Classics Workshop',
      start_time: '15:00',
      end_time: '17:00',
      menu_id: 106,
      capacity: 8,
      price: 1300,
      is_special: true,
      enrolled: 6
    },
        {
      id: 9,
      class_date: '2025-11-13',
      session: 'Sweet Thai Classics Workshop',
      start_time: '15:00',
      end_time: '17:00',
      menu_id: 106,
      capacity: 8,
      price: 1300,
      is_special: true,
      enrolled: 6
    },
        {
      id: 10,
      class_date: '2025-11-13',
      session: 'Sweet Thai Classics Workshop',
      start_time: '15:00',
      end_time: '17:00',
      menu_id: 106,
      capacity: 8,
      price: 1300,
      is_special: true,
      enrolled: 6
    },
        {
      id: 11,
      class_date: '2025-11-13',
      session: 'Sweet Thai Classics Workshop',
      start_time: '15:00',
      end_time: '17:00',
      menu_id: 106,
      capacity: 8,
      price: 1300,
      is_special: true,
      enrolled: 6
    }
  ]);

  const [showForm, setShowForm] = useState<boolean>(false);
  const [editingId, setEditingId] = useState<number | null>(null);
  const [showDeleteModal, setShowDeleteModal] = useState<boolean>(false);
  const [deletingId, setDeletingId] = useState<number | null>(null);
  const [searchQuery, setSearchQuery] = useState<string>('');
  const [currentPage, setCurrentPage] = useState<number>(1);
  const itemsPerPage = 10;
  
  const [formData, setFormData] = useState<FormData>({
    class_date: '',
    session: '',
    start_time: '',
    end_time: '',
    menu_id: 0,
    capacity: 0,
    price: 0,
    is_special: false,
    enrolled: 0,
    detail: ''
  });

  const handleAddCourse = (): void => {
    setEditingId(null);
    setFormData({ 
      class_date: '', 
      session: '', 
      start_time: '', 
      end_time: '', 
      menu_id: 0, 
      capacity: 0, 
      price: 0, 
      is_special: false, 
      enrolled: 0,
      detail: ''
    });
    setShowForm(true);
  };

  const handleEditCourse = (course: Course): void => {
    setEditingId(course.id);
    setFormData({
      class_date: course.class_date,
      session: course.session,
      start_time: course.start_time,
      end_time: course.end_time,
      menu_id: course.menu_id,
      capacity: course.capacity,
      price: course.price,
      is_special: course.is_special,
      enrolled: course.enrolled || 0,
      detail: course.detail || ''
    });
    setShowForm(true);
  };

  const handleSaveCourse = (): void => {
    if (editingId) {
      setCourses(courses.map(c => c.id === editingId ? { ...c, ...formData } : c));
    } else {
      setCourses([...courses, { id: Date.now(), ...formData }]);
    }
    setShowForm(false);
    setFormData({ 
      class_date: '', 
      session: '', 
      start_time: '', 
      end_time: '', 
      menu_id: 0, 
      capacity: 0, 
      price: 0, 
      is_special: false, 
      enrolled: 0,
      detail: ''
    });
  };

  const handleDeleteCourse = (id: number): void => {
    setDeletingId(id);
    setShowDeleteModal(true);
  };

  const confirmDelete = (): void => {
    if (deletingId) {
      setCourses(courses.filter(c => c.id !== deletingId));
      setShowDeleteModal(false);
      setDeletingId(null);
    }
  };

  const cancelDelete = (): void => {
    setShowDeleteModal(false);
    setDeletingId(null);
  };

  // Filter and Pagination Logic
  const filteredCourses = useMemo(() => {
    return courses.filter(course => {
      const searchLower = searchQuery.toLowerCase();
      return (
        course.session.toLowerCase().includes(searchLower) ||
        course.class_date.includes(searchQuery) ||
        course.menu_id.toString().includes(searchQuery)
      );
    });
  }, [courses, searchQuery]);

  const totalPages = Math.ceil(filteredCourses.length / itemsPerPage);
  
  const paginatedCourses = useMemo(() => {
    const startIndex = (currentPage - 1) * itemsPerPage;
    return filteredCourses.slice(startIndex, startIndex + itemsPerPage);
  }, [filteredCourses, currentPage]);

  const handlePageChange = (page: number): void => {
    setCurrentPage(page);
  };

  const handleSearchChange = (query: string): void => {
    setSearchQuery(query);
    setCurrentPage(1);
  };

  const formatTime = (time: string): string => {
    if (!time) return '';
    const [hours, minutes] = time.split(':');
    const hour = parseInt(hours);
    const ampm = hour >= 12 ? 'PM' : 'AM';
    const displayHour = hour % 12 || 12;
    return `${displayHour}:${minutes} ${ampm}`;
  };

  const formatDate = (dateStr: string): string => {
    if (!dateStr) return '';
    const date = new Date(dateStr + 'T00:00:00');
    return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' }).toUpperCase();
  };

  return (
<div style={{ display: 'flex'}}>
 <SidebarOnly />
    <div className="p-8" style={{ backgroundColor: '#f5f1ed' }}>
     
      <div className="max-w-full">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-4xl font-light mb-2" style={{ color: '#3d2817' }}>Manage Courses</h1>
          <p className="text-sm" style={{ color: '#8b6f47' }}>Nal Baan Cooking Studio</p>
        </div>

        {/* Course Table */}
        <div className="bg-white rounded-lg shadow-md border" style={{ borderColor: '#e5dcd4' }}>
          <div className="px-8 py-6 border-b" style={{ borderColor: '#e5dcd4' }}>
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-xl font-light" style={{ color: '#3d2817' }}>All Courses</h3>
              <button
                onClick={handleAddCourse}
                className="px-4 py-2 rounded text-white flex items-center gap-2 font-light text-sm hover:shadow transition"
                style={{ backgroundColor: '#3d2817' }}
              >
                <Plus size={16} /> Add Course
              </button>
            </div>
            
            {/* Search Bar */}
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2" size={18} style={{ color: '#8b6f47' }} />
              <input
                type="text"
                value={searchQuery}
                onChange={(e) => handleSearchChange(e.target.value)}
                placeholder="Search by session name, date, or menu ID..."
                className="w-full pl-10 pr-4 py-2 border rounded-lg text-sm focus:outline-none transition"
                style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
              />
            </div>
          </div>

          <div className="overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr style={{ backgroundColor: '#f5f1ed', borderBottom: '1px solid #e5dcd4' }}>
                  <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Session</th>
                  <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Date</th>
                  <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Time</th>
                  <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Capacity</th>
                  <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Enrolled</th>
                  <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Price</th>
                  <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Detail</th>
                  <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Actions</th>
                </tr>
              </thead>
              <tbody>
                {paginatedCourses.length > 0 ? (
                  paginatedCourses.map(course => (
                    <tr key={course.id} className="border-b hover:opacity-75 transition" style={{ borderColor: '#e5dcd4' }}>
                      <td className="px-6 py-4 text-sm" style={{ color: '#3d2817' }}>
                        <div className="flex items-center gap-2">
                          {course.session}
                          {course.is_special && <span className="text-xs px-2 py-0.5 rounded-full" style={{ backgroundColor: '#d4a574', color: 'white' }}>Special</span>}
                        </div>
                      </td>
                      <td className="px-6 py-4 text-sm" style={{ color: '#8b6f47' }}>{formatDate(course.class_date)}</td>
                      <td className="px-6 py-4 text-sm" style={{ color: '#8b6f47' }}>{formatTime(course.start_time)} - {formatTime(course.end_time)}</td>
                      <td className="px-6 py-4 text-sm" style={{ color: '#8b6f47' }}>{course.capacity}</td>
                      <td className="px-6 py-4 text-sm">
                        <span className="px-3 py-1 rounded-full text-xs font-light" style={{
                          backgroundColor: (course.enrolled || 0) >= course.capacity ? '#f5d5d5' : '#e8f5e9',
                          color: (course.enrolled || 0) >= course.capacity ? '#c1513b' : '#558b2f'
                        }}>
                          {course.enrolled || 0}/{course.capacity}
                        </span>
                      </td>
                      <td className="px-6 py-4 text-sm font-light" style={{ color: '#3d2817' }}>฿{course.price}</td>
                      <td className="px-6 py-4 text-sm" style={{ color: '#8b6f47' }}>
                        {course.detail ? (
                          <div 
                            className="max-w-xs truncate"
                            dangerouslySetInnerHTML={{ __html: course.detail }}
                          />
                        ) : (
                          <span className="text-gray-400 italic">No detail</span>
                        )}
                      </td>
                      <td className="px-6 py-4 text-sm space-x-2 flex">
                        <button
                          onClick={() => handleEditCourse(course)}
                          className="p-2 rounded transition"
                          style={{ color: '#3d2817', backgroundColor: '#f5f1ed' }}
                        >
                          <Edit size={16} />
                        </button>
                        <button
                          onClick={() => handleDeleteCourse(course.id)}
                          className="p-2 rounded transition"
                          style={{ color: '#c1513b', backgroundColor: '#f5f1ed' }}
                        >
                          <Trash2 size={16} />
                        </button>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={8} className="px-6 py-12 text-center text-sm" style={{ color: '#8b6f47' }}>
                      No courses found
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>

          {/* Pagination */}
          {totalPages > 1 && (
            <div className="px-8 py-4 border-t flex items-center justify-between" style={{ borderColor: '#e5dcd4' }}>
              <div className="text-sm" style={{ color: '#8b6f47' }}>
                Showing {((currentPage - 1) * itemsPerPage) + 1} to {Math.min(currentPage * itemsPerPage, filteredCourses.length)} of {filteredCourses.length} courses
              </div>
              
              <div className="flex gap-2">
                <button
                  onClick={() => handlePageChange(currentPage - 1)}
                  disabled={currentPage === 1}
                  className="px-3 py-1 rounded border transition disabled:opacity-40 disabled:cursor-not-allowed"
                  style={{ 
                    borderColor: '#e5dcd4',
                    color: '#3d2817',
                    backgroundColor: currentPage === 1 ? '#f5f1ed' : 'white'
                  }}
                >
                  <ChevronLeft size={18} />
                </button>
                
                {[...Array(totalPages)].map((_, i) => (
                  <button
                    key={i + 1}
                    onClick={() => handlePageChange(i + 1)}
                    className="px-4 py-1 rounded border transition"
                    style={{
                      borderColor: '#e5dcd4',
                      backgroundColor: currentPage === i + 1 ? '#3d2817' : 'white',
                      color: currentPage === i + 1 ? 'white' : '#3d2817'
                    }}
                  >
                    {i + 1}
                  </button>
                ))}
                
                <button
                  onClick={() => handlePageChange(currentPage + 1)}
                  disabled={currentPage === totalPages}
                  className="px-3 py-1 rounded border transition disabled:opacity-40 disabled:cursor-not-allowed"
                  style={{ 
                    borderColor: '#e5dcd4',
                    color: '#3d2817',
                    backgroundColor: currentPage === totalPages ? '#f5f1ed' : 'white'
                  }}
                >
                  <ChevronRight size={18} />
                </button>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Add/Edit Modal */}
      {showForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-8 max-w-md w-full mx-4 shadow-lg" style={{ backgroundColor: '#fffbf7' }}>
            <h3 className="text-2xl font-light mb-6" style={{ color: '#3d2817' }}>
              {editingId ? 'Edit Course' : 'Add New Course'}
            </h3>

            <div className="space-y-4">
              <div>
                <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Session Name</label>
                <input
                  type="text"
                  value={formData.session}
                  onChange={(e) => setFormData({ ...formData, session: e.target.value })}
                  className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                  style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                  placeholder="e.g., Thai Herbs & Spices Cooking Class"
                />
              </div>

              <div>
                <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Class Date</label>
                <input
                  type="date"
                  value={formData.class_date}
                  onChange={(e) => setFormData({ ...formData, class_date: e.target.value })}
                  className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                  style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Start Time</label>
                  <input
                    type="time"
                    value={formData.start_time}
                    onChange={(e) => setFormData({ ...formData, start_time: e.target.value })}
                    className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                    style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                  />
                </div>
                <div>
                  <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>End Time</label>
                  <input
                    type="time"
                    value={formData.end_time}
                    onChange={(e) => setFormData({ ...formData, end_time: e.target.value })}
                    className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                    style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Menu ID</label>
                <input
                  type="number"
                  value={formData.menu_id}
                  onChange={(e) => setFormData({ ...formData, menu_id: parseInt(e.target.value) })}
                  className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                  style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                  placeholder="101"
                />
              </div>

              <div className="grid grid-cols-3 gap-4">
                <div>
                  <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Capacity</label>
                  <input
                    type="number"
                    value={formData.capacity}
                    onChange={(e) => setFormData({ ...formData, capacity: parseInt(e.target.value) })}
                    className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                    style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                    placeholder="12"
                  />
                </div>

                <div>
                  <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Enrolled</label>
                  <input
                    type="number"
                    value={formData.enrolled || 0}
                    onChange={(e) => setFormData({ ...formData, enrolled: parseInt(e.target.value) })}
                    className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                    style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                    placeholder="0"
                  />
                </div>

                <div>
                  <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Price (฿)</label>
                  <input
                    type="number"
                    value={formData.price}
                    onChange={(e) => setFormData({ ...formData, price: parseInt(e.target.value) })}
                    className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                    style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                    placeholder="1500"
                  />
                </div>
              </div>

              <div className="flex items-center gap-3 p-3 rounded-lg" style={{ backgroundColor: '#f5f1ed' }}>
                <input
                  type="checkbox"
                  checked={formData.is_special}
                  onChange={(e) => setFormData({ ...formData, is_special: e.target.checked })}
                  className="w-4 h-4"
                  id="is_special"
                />
                <label htmlFor="is_special" className="text-sm font-light" style={{ color: '#8b6f47' }}>
                  Mark as Special Class
                </label>
              </div>

              <div>
                <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Detail (HTML)</label>
                <textarea
                  value={formData.detail || ''}
                  onChange={(e) => setFormData({ ...formData, detail: e.target.value })}
                  className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition font-mono"
                  style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                  placeholder="<h3>Title</h3><p>Description...</p>"
                  rows={6}
                />
                <p className="text-xs mt-1" style={{ color: '#8b6f47' }}>You can use HTML tags here</p>
              </div>
            </div>

            <div className="flex gap-3 mt-6">
              <button
                onClick={() => setShowForm(false)}
                className="flex-1 px-4 py-2 rounded-lg font-light transition hover:opacity-80"
                style={{ color: '#3d2817', backgroundColor: '#e5dcd4' }}
              >
                Cancel
              </button>
              <button
                onClick={handleSaveCourse}
                className="flex-1 px-4 py-2 rounded-lg text-white font-light transition hover:shadow-lg"
                style={{ backgroundColor: '#3d2817' }}
              >
                {editingId ? 'Update' : 'Save'} Course
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Delete Confirmation Modal */}
      {showDeleteModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-8 max-w-md w-full mx-4 shadow-lg" style={{ backgroundColor: '#fffbf7' }}>
            <h3 className="text-2xl font-light mb-4" style={{ color: '#3d2817' }}>Confirm Delete</h3>
            
            <p className="text-sm mb-6" style={{ color: '#8b6f47' }}>
              Are you sure you want to delete this course? This action cannot be undone.
            </p>

            <div className="flex gap-3">
              <button
                onClick={cancelDelete}
                className="flex-1 px-4 py-2 rounded-lg font-light transition hover:opacity-80"
                style={{ color: '#3d2817', backgroundColor: '#e5dcd4' }}
              >
                Cancel
              </button>
              <button
                onClick={confirmDelete}
                className="flex-1 px-4 py-2 rounded-lg text-white font-light transition hover:shadow-lg"
                style={{ backgroundColor: '#c1513b' }}
              >
                Delete
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
</div>
    
 
  );
}